"use strict";
var Tree = /** @class */ (function () {
    function Tree(data) {
        this._data = data;
    }
    Object.defineProperty(Tree.prototype, "data", {
        /**
         * 返回data
         */
        get: function () {
            return this._data;
        },
        enumerable: true,
        configurable: true
    });
    Object.defineProperty(Tree.prototype, "html", {
        get: function () {
            return this._tree;
        },
        enumerable: true,
        configurable: true
    });
    /**
     * 创建一个树形组件
     */
    Tree.prototype.create = function () {
        var _this = this;
        var ul = this._curateUl();
        this._container = ul;
        ul.className = "tree-container";
        this._data.forEach(function (treeItem) {
            var li = _this._createLi();
            _this._container.appendChild(li);
            li.className = "tree-item";
            li.innerText = treeItem.element;
            li.id = "tree-item-" + treeItem.id;
        }, this);
        this._tree = this._container;
    };
    /**
     * 刷新数据,重新生成一个树
     */
    Tree.prototype.fresh = function (data) {
        this._data = data;
    };
    /**
     * 组件的ul部分
     */
    Tree.prototype._curateUl = function () {
        return document.createElement('ul');
    };
    /**
     * 组件的li部分html
     */
    Tree.prototype._createLi = function () {
        return document.createElement('li');
    };
    return Tree;
}());
var data = [
    { element: '轮播', id: 2, pid: 1 },
    { element: '轮播小图', id: 3, pid: 2 },
    { element: '首页', id: 1, pid: 0 },
    { element: '轮播大图', id: 4, pid: 2 },
    { element: '产品', id: 5, pid: 0 },
    { element: '蔬菜', id: 6, pid: 5 },
    { element: '有机蔬菜', id: 7, pid: 6 },
    { element: '无机蔬菜', id: 8, pid: 6 },
    { element: '水果', id: 9, pid: 5 },
    { element: '有机水果', id: 10, pid: 9 },
    { element: '有机大水果', id: 11, pid: 10 },
    { element: '有机小水果', id: 12, pid: 10 },
    { element: '无机水果', id: 13, pid: 9 },
];
// 单元测试
var tree = new Tree(data);
tree.create();
console.log(tree.html);
console.log(tree.data);



